lite-youtube {
  contain: content;
  cursor: pointer;
  max-width: 720px;
  background-color: #000;
  background-position: center;
  background-size: cover;
  display: block;
  position: relative;
}

lite-youtube:before {
  content: "";
  height: 60px;
  width: 100%;
