  background-position: top;
  background-repeat: repeat-x;
  padding-bottom: 50px;
  transition: all .2s cubic-bezier(0, 0, .2, 1);
  display: block;
  position: absolute;
  top: 0;
}

lite-youtube:after {
  content: "";
  padding-bottom: 56.25%;
  display: block;
}

